@use "../component/navigation";
@use "../abstract/index" as *;

body {
  min-height: 100vh;
  font-family: "Barlow";
  background: url(../../images/home/background-home-mobile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  // padding-bottom: 3rem;
}

.container {
  max-width: 1110px;
  margin-inline: auto;
}

.main {
  text-align: center;
  margin-top: 1.5rem;
  padding-inline: 1.5rem;
}

.hero__title {
  font-family: "Barlow Condensed", sans-serif;
  text-transform: uppercase;
  color: color(accent-color);
  font-weight: normal;
  line-height: 19px;
  letter-spacing: 2.7px;
  font-size: 1rem;
}

.hero__title--featured {
  margin: 0;
  padding: 0;
  display: block;
  font-family: "Bellefair", sans-serif;
  text-transform: uppercase;
  font-size: 80px;
  font-weight: normal;
  line-height: 125%;
  color: color(secondary-color);
  padding: 1rem;
}

.hero__text {
  font-family: "Barlow", sans-serif;
  font-size: 15px;
  font-weight: normal;
  line-height: 25px;
  color: color(accent-color);
  margin-inline: auto;
  max-width: 400px;
}

.hero__button {
  display: block;
  cursor: pointer;
  position: relative;
  z-index: 10;
  background: color(secondary-color);
  margin-inline: auto;
  margin-top: 81px;
  padding: 68px 29.5px;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  text-decoration: none;

  &:before {
    z-index: 1;
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: color(secondary-color);
    opacity: 0.1;
    top: 50%;
    left: 50%;
    transform-origin: center;
    transform: translate(-50%, -50%) scale(0);
    border-radius: 50%;
    transition: all 0.2s ease-in-out;
  }

  p {
    align-self: center;
    position: relative;
    z-index: 100;
    font-family: "Bellefair", sans-serif;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
    text-decoration: none;
    color: color(primary-color);
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 1.25px;
    font-weight: normal;
  }

  &:hover,
  &:focus-visible {
    &:before {
      transform: translate(-50%, -50%) scale(1.55);
    }
  }
}

@media (min-width: 768px) {
  body {
    background-image: url(../../images/home/background-home-tablet.jpg);
    padding-top: 0.05px;
  }
  .main {
    margin-top: 106px;
    margin-inline: auto;
    width: min-content;
    padding-bottom: 60px;
  }

  .hero__title {
    font-size: 20px;
    letter-spacing: 3.375px;
    line-height: 24px;
  }

  .hero__title--featured {
    padding: 0;
    padding-block: 1.5rem;
    font-size: 150px;
    line-height: 150px;
  }

  .hero__text {
    max-width: 100%;
    font-size: 16px;
    line-height: 28px;
    font-weight: 400;
  }

  .hero__button {
    margin-top: 156px;
    width: 242px;
    height: 242px;
    padding: 103px 48px 102px 46px;

    p {
      font-size: 32px;
      line-height: 37px;
      letter-spacing: 2px;
    }
  }
}

@media (min-width: 1100px) {
  body {
    background-image: url(../../images/home/background-home-desktop.jpg);
  }

  .main {
    padding-inline: 5rem;
    width: 100%;
    margin-top: 20vh;
    padding-bottom: 131px;
    margin-top: 27.9vh;
    // @media (min-width: 1400px) {
    // margin-top: 27.9vh;
    // }
  }

  .hero {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
  }

  .hero__content {
    width: min-content;
  }

  .hero__title {
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;
    text-align: left;
  }

  .hero__title--featured {
    font-size: 150px;
    line-height: 172px;
  }

  .hero__text {
    text-align: left;
    font-size: 18px;
    line-height: 32px;
  }

  .hero__button {
    margin: 0;
    justify-self: end;
    align-self: end;
    width: 274px;
    height: 274px;
    padding: 119px 64px 118px 62px;
    margin-right: 24px;
    @media (min-width: 1300px) {
      margin: 0;
    }

    p {
      font-size: 32px;
      line-height: 37px;
      letter-spacing: 2px;
    }
  }
}
